.Sessions-navy-input {
  width: 100%;
  height: 48px;
  padding: 10px;
  border: 1px solid #0E1C2E;
  outline: none;
  background: #0E1C2E;
  color: #FFF;
  font-size: 14px;
  font-weight: 300; }
  .Sessions-navy-input::-webkit-input-placeholder {
    color: #B1B8C1; }
  .Sessions-navy-input:-moz-placeholder {
