  .Sessions-navy-input::-moz-placeholder {
    color: #B1B8C1; }
  .Sessions-navy-input:-ms-input-placeholder {
    color: #B1B8C1; }
  .Sessions-navy-input:focus {
    border: 1px solid #1785FB; }
  .Sessions-navy-input.inputWithError {
