.Sessions .Sessions-inputLinkAside {
  position: absolute;
  top: 0;
  right: 0;
  height: 48px;
  margin-right: 10px;
  color: #1785FB;
  font-size: 14px;
  font-weight: 200;
  line-height: 50px; }
  .Sessions .Sessions-inputLinkAside a {
    color: #1785FB; }

.Sessions-navy .Sessions-inputLinkAside {
  position: absolute;
  top: 0;
  right: 0;
  height: 48px;
  margin-right: 10px;
  color: #FFF;
  font-size: 14px;
  font-weight: 200;
  line-height: 50px; }
  .Sessions-navy .Sessions-inputLinkAside a {
