.Sessions-fieldError {
  position: absolute;
  top: 14px;
  right: -32px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: #F24440;
  color: #FFF;
  font-weight: 400;
  line-height: 22px;
  text-align: center; }
  .Sessions-fieldError:hover {
    cursor: pointer; }

.Loading-item {
  display: inline-block;
  position: relative;
  width: 8px;
  height: 8px;
  margin-left: 1px;
  list-style-type: none; }
  .Loading-item:first-child {
    margin-left: 0; }

.Loading-itemInner {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 8px;
  height: 8px;
  -webkit-animation-name: loading;
  animation-name: loading;
  -webkit-animation-duration: 0.7s;
  animation-duration: 0.7s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  border-radius: 50%;
  opacity: 0.2;
  background: #FFF; }

.Loading-itemInner--01 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.Loading-itemInner--02 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.Loading-itemInner--03 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

.tipsy {
  font-size: 13px; }

.Sessions-referrer {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 60px;
  margin-top: -30px;
  margin-left: -150px;
  border-radius: 4px;
  background: #FFF;
  text-align: center; }
  .Sessions-referrer picture,
  .Sessions-referrer img {
    display: inline-block;
    width: 100px;
    height: 50px;
    vertical-align: middle; }

.Sessions-referrerItem {
  display: inline-block;
  width: 100px;
  height: 50px; }

.Sessions-footer {
  margin-bottom: 32px; }

@media (max-width: 960px) {
  .Sessions-inner {
    justify-content: center; }
  .Sessions-right {
    display: none; } }

@media (max-width: 425px) {
  .Sessions-fieldError {
    display: none; }
  .Sessions-input.inputWithError {
    border-color: #F24440; } }

@media (max-width: 960px) {
  .Sessions-inner {
    justify-content: center; }
  .Sessions-right {
    display: none; } }

@-webkit-keyframes loading {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    opacity: 0.2; }
  50% {
    -webkit-transform: scale3d(1.4, 1.4, 1);
    opacity: 0.6; }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
    opacity: 0.2; } }

@keyframes loading {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2; }
  50% {
    transform: scale3d(1.4, 1.4, 1);
    opacity: 0.6; }
  100% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2; } }
